import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject } from '@angular/core';
// import { ProfessorsService } from '../../../all-professors/professors.service';
import { ComplaintService } from '../../complaint.service';
import { Complaint } from '../../complaint';
@Component({
  selector: 'app-complaint-delete',
  templateUrl: './complaint-delete.component.html',
  styleUrls: ['./complaint-delete.component.sass']
})
export class ComplaintDeleteComponent  {

  constructor(
    public dialogRef: MatDialogRef<ComplaintDeleteComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public complaintService: ComplaintService
  ) {}
  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDelete(): void {
    this.complaintService.deleteComplaint(this.data.id);
  }

}
