import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { FeeReport, Student } from './fees.model';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { finalize, tap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

export class Classes{
  constructor(
      public id?: any,
      public name?: string,
      public available?: any
    ) {}
}

@Injectable()
export class FeesService {
  API_URL = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.API_URL;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;
  private readonly API_CLASS = 'assets/data/my-classes.json';
  private readonly API_STUDENT = 'assets/data/my-students.json';

  private readonly API_URLS = 'assets/data/report.json';
  dataChange: BehaviorSubject<FeeReport[]> = new BehaviorSubject<FeeReport[]>([]);
  // Temporarily stores data from dialogs
  dialogData: any;
  constructor(private httpClient: HttpClient) {}
  get data(): FeeReport[] {
    return this.dataChange.value;
  }
  getDialogData() {
    return this.dialogData;
  }
  /** CRUD METHODS */
  getAllFeess(): void {
    this.httpClient.get<FeeReport[]>(this.baseurl + '/fee/create/fees_report/?expand=classes,student_id,fee_category').subscribe(
      data => {
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }
  // DEMO ONLY, you can find working methods below
  addFees(fees: FeeReport): void {
    this.dialogData = fees;
    // this.createFeeReport(fees);
  }
  updateFees(fees: FeeReport): void {
    this.dialogData = fees;
    // this.editFeeReport(fees);
  }
  deleteFees(id: number): void {
    console.log(id);
  }
  createFeeReport(
    // fees: FeeReport
    student_name: string,
    classes: string,
    total_fees_assigned: any,
    total_amount_paid: any,
    balance_to_pay: any,
    payment_due_date: any,
    last_payment_date: any,
    status: any
    
  ): Observable<FeeReport> {
    const url = this.baseurl + '/fee/create/fees_report/';
    const formData = new FormData();
    formData.append('student_name', student_name);
    formData.append('classes', classes);
    formData.append('total_fees_assigned', total_fees_assigned);
    formData.append('total_amount_paid', total_amount_paid);
    formData.append('balance_to_pay', balance_to_pay);
    // formData.append('payment_due_date', payment_due_date);
    // formData.append('last_payment_date', last_payment_date);
    formData.append('status', status);
    return this.httpClient.request<FeeReport>('POST', url, {body: formData});
  }

  editFeeReport(
    // fees : FeeReport
    id: any,
    student_name: string,
    classes: string,
    total_fees_assigned: any,
    total_amount_paid: any,
    balance_to_pay: any,
    payment_due_date: any,
    last_payment_date: any,
    status: any
  ): Observable<FeeReport> {
    const url = this.baseurl + '/fee/edit/fees_report/' + id + '/';
    const formData = new FormData();
    formData.append('student_name', student_name);
    formData.append('classes', classes);
    formData.append('total_fees_assigned', total_fees_assigned);
    formData.append('total_amount_paid', total_amount_paid);
    formData.append('balance_to_pay', balance_to_pay);
    // formData.append('payment_due_date', payment_due_date);
    // formData.append('last_payment_date', last_payment_date);
    formData.append('status', status);
    return this.httpClient.request<FeeReport>('PUT', url, {body: formData});
  }

  getFeeReport(): Observable<FeeReport> {
    const url = this.baseurl + '/fee/create/fees_report/';
    return this.httpClient.request<FeeReport>('GET', url);
  }

  // getClasses(): Observable<Classes[]> {
  //   const url = this.API_CLASS;
  //   return this.httpClient.request<Classes[]>('GET', url);
  // }

  // getStudents(): Observable<Student[]> {
  //   const url = this.API_STUDENT;
  //   return this.httpClient.request<Student[]>('GET', url);
  // }

  getClasses(): Observable<Classes[]> {
    const url = this.baseurl + '/student/create/class/';
    return this.httpClient.request<Classes[]>('GET', url);
  }

  getStudents(): Observable<Student[]> {
    const url = this.baseurl + '/student/create/student_exam/';
    return this.httpClient.request<Student[]>('GET', url);
  }
}
