import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject } from '@angular/core';
// import { ProfessorsService } from '../../../all-professors/professors.service';
import { EnquiryService } from '../../enquiry.service';
// import { ProfessorsService, PhoneCallLog } from '../../professors.service';
import { Enquiry } from '../../enquiry';

@Component({
  selector: 'app-admission-enquiry-delete',
  templateUrl: './admission-enquiry-delete.component.html',
  styleUrls: ['./admission-enquiry-delete.component.sass']
})
export class AdmissionEnquiryDeleteComponent {

  constructor(
    public dialogRef: MatDialogRef<AdmissionEnquiryDeleteComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public enquiryService: EnquiryService
  ) {}
  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDelete(): void {
    this.enquiryService.deleteEnquiry(this.data.id);
  }


}
