import { formatDate } from '@angular/common';
export class PhoneLogs {
  id: any;
  name: string;
  date: any;
  time: any;
  call_duration: any;
  description: string;
  phone_number: any;
  follow_up_date: any;
  note: string;
  call_type: string
  constructor(visitor) {
      this.id = visitor.id || '';
      this.name = visitor.name || '';
      this.time = visitor.time || '';
      this.call_duration = visitor.call_duration || '';
      this.date = formatDate(new Date(), 'yyyy-MM-dd', 'en') || '';
      this.follow_up_date = formatDate(new Date(), 'yyyy-MM-dd', 'en') || '';
      this.description = visitor.description || '';
      this.phone_number = visitor.phone_number || '';
      this.note = visitor.note || '';
      this.call_type = visitor.call_type || '';
    }
}

