import { formatDate } from '@angular/common';
export class Enquiry {
   id: any;
   name: string;
   phone_number: string;
   email: string;
   address: string;
  //  user_subdomain?: string;
   description: string;
   note: string;
   no_child_to_enroll: string;
   date: any;
   reference: string;
   follow_up_date: any;
   status: string;
   admin_note: string;
    constructor(enquiry) {
      this.id = enquiry.id || '';
      this.name = enquiry.name || '';
      this.phone_number = enquiry.phone_number || '';
      this.email = enquiry.email || '';
      this.address = enquiry.address || '';
      this.note = enquiry.note || '';
      this.date = formatDate(new Date(), 'yyyy-MM-dd', 'en') || '';
      this.no_child_to_enroll = enquiry.no_child_to_enroll || '';
      this.reference = enquiry.reference || '',
      this.follow_up_date = formatDate(new Date(), 'yyyy-MM-dd', 'en') || '';
      this.status = enquiry.status || '';
      this.admin_note = enquiry.admin_note || '';

    }
}
