@Injectable({
  providedIn: 'root'
})
export class VisitorsService {

  API_URL = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.API_URL;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;
  host = window.location.origin;
  // private readonly API_URL = 'assets/data/professors.json';
  dataChange: BehaviorSubject<VisitorBook[]> = new BehaviorSubject<VisitorBook[]>(
    []
  );
  // Temporarily stores data from dialogs
  dialogData: any;
  constructor(private httpClient: HttpClient) {}
  get data(): VisitorBook[] {
    return this.dataChange.value;
  }
  getDialogData() {
    return this.dialogData;
  }
  /** CRUD METHODS */
  getAllVisitorBook(): void {
    console.log(this.host + " is my host");
    this.httpClient.get<VisitorBook[]>(this.baseurl + '/web/create/visitor_book/').subscribe(
      data => {
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  
  // DEMO ONLY, you can find working methods below
  addVisitor(visitor: VisitorBook): void {
    this.dialogData = visitor;
    this.createVisitorBook(visitor);
  }
  updateVisitor(visitor: VisitorBook): void {
    this.editVisitorBook(visitor);
    this.dialogData = visitor;
  }
  deleteVisitor(id: any): void {
    console.log(id);
    this.deleteAVisitorBook(id);
  }

  createVisitorBook(
    visitor: VisitorBook
  ): Observable<VisitorBook> {
    const url = this.baseurl + '/web/create/visitor_book/';
    const formData = new FormData();
    formData.append('name', visitor.name);
    formData.append('phone_number', visitor.phone_number);
    formData.append('purpose', visitor.purpose);
    // formData.append('picture', visitor.picture);
    formData.append('note', visitor.note);
    formData.append('approved', visitor.approved);
    formData.append('whom_to_see', visitor.whom_to_see)
    return this.httpClient.request<VisitorBook>('POST', url, {body: formData});
  }

  editVisitorBook(
    visitor: VisitorBook
  ): Observable<VisitorBook> {
    const url = this.baseurl + '/web/edit/visitor_book/' + visitor.id + '/';
    const formData = new FormData();
    formData.append('name', visitor.name);
    formData.append('phone_number', visitor.phone_number);
    formData.append('purpose', visitor.purpose);
    // formData.append('picture', visitor.picture);
    formData.append('note', visitor.note);
    formData.append('approved', visitor.approved);
    formData.append('whom_to_see', visitor.whom_to_see)
    return this.httpClient.request<VisitorBook>('PUT', url, {body: formData});
  }

  deleteAVisitorBook(
    id: any
  ): void {
    console.log("deleted");
    this.httpClient.delete<VisitorBook>(this.baseurl + '/web/edit/visitor_book/' + id + '/').subscribe(
      data => {
        console.log(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  
  
}
