import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Component, Inject } from '@angular/core';
import { ProfessorsService, PhoneCallLog } from '../../professors.service';
import {
  FormControl,
  Validators,
  FormGroup,
  FormBuilder
} from '@angular/forms';
import { Professors } from '../../professors.model';
import { formatDate } from '@angular/common';
@Component({
  selector: 'app-form-dialog',
  templateUrl: './form-dialog.component.html',
  styleUrls: ['./form-dialog.component.sass']
})
export class FormDialogComponent {
  action: string;
  dialogTitle: string;
  proForm: FormGroup;
  professors: PhoneCallLog;
  constructor(
    public dialogRef: MatDialogRef<FormDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public professorsService: ProfessorsService,
    private fb: FormBuilder
  ) {
    // Set the defaults
    this.action = data.action;
    if (this.action === 'edit') {
      this.dialogTitle = data.professors.name;
      this.professors = data.professors;
    } else {
      this.dialogTitle = 'New Phone Call Log';
      this.professors = new Professors({});
    }
    this.proForm = this.createContactForm();
  }
  formControl = new FormControl('', [
    Validators.required
    // Validators.email,
  ]);
  getErrorMessage() {
    return this.formControl.hasError('required')
      ? 'Required field'
      : this.formControl.hasError('email')
      ? 'Not a valid email'
      : '';
  }
  createContactForm(): FormGroup {
    return this.fb.group({
      id: [this.professors.id],
      img: [this.professors.call_duration],
      name: [this.professors.name],
      // email: [
      //   this.professors.,
      //   [Validators.required, Validators.email, Validators.minLength(5)]
      // ],
      date: [ this.professors.follow_up_date
        // formatDate(this.professors.follow_up_date, 'yyyy-MM-dd', 'en'),
        // [Validators.required]
      ],
      gender: [this.professors.call_type],
      mobile: [this.professors.description],
    });
  }
  submit() {
    // emppty stuff
    
  }
  
  onNoClick(): void {
    this.dialogRef.close();
  }
  public confirmAdd(): void {
    this.professorsService.addProfessors(this.proForm.getRawValue());
    this.professorsService.createPhoneCallLog(
      // user_subdomain,
      this.professors.name,
      this.professors.call_duration,
      this.professors.description,
      this.professors.follow_up_date,
      this.professors.note,
      this.professors.call_type

    ).subscribe((res) => {
      // this.router.navigateByUrl('/create-client/' + `${res.data.id}`);
      console.log(res);
      // console.log(res.data.id);
      console.log("successfully logged in");
      // this.success = res;
      // this.success = "Your super admin account has been successfully created"
      
    }, (error) => {
      console.error(error);
      // this.error = error.error;
    });

  }
}
