import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Professors } from './professors.model';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { finalize, tap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';

export class Setup {
  constructor(
    public id?: any,
    public template?: string,
    public about_us?: string,
    public contact_us?: string,
    public header_content?: string,
    // public user_subdomain?: string,
    public footer_content?: string,
    public color?: string,
    public date?: any,
    public time?: any,
  ) {}
}

export class AdmissionEnquiry {
  constructor(
    public id?: any,
    public name?: string,
    public phone_number?: string,
    public email?: string,
    public address?: string,
    // public user_subdomain?: string,
    public description?: string,
    public note?: string,
    public no_child_to_enroll?: string,
    public date?: any,
    public reference?: string,
    public follow_up_date?: any,
    public status?: string,
    public admin_note?: string,
  ) {}
}

export class VistorBook {
  constructor(
    public id?: any,
    public name?: string,
    public phone_number?: string,
    public purpose?: string,
    public picture?: any,
    // public user_subdomain?: string,
    public date?: any,
    public in_time?: any,
    public out_time?: any,
    public note?: string,
    public approved?: boolean,
  ) {}
}

export class PhoneCallLog {
  constructor(
    public id?: any,
    public name?: string,
    public date?: any,
    public time?: any,
    public call_duration?: any,
    public description?: string,
    public follow_up_date?: any,
    public note?: string,
    public call_type?: string
  ) {}
}

export class Correspondence {
  constructor(
    public id?: any,
    public from_title?: string,
    public to_title?: any,
    public reference_no?: any,
    public address?: any,
    public note?: string,
    public date?: any,
    public attach_document?: any,
    public call_type?: string
  ) {}
}

export class Complaint {
  constructor(
    public id?: any,
    public complain_type?: string,
    public source?: any,
    public phone?: any,
    public date?: any,
    public description?: string,
    public action_taken?: string,
    public note?: string,
    public attach_document?: any,
    public status?: string,
  ) {}
}


@Injectable()
export class ProfessorsService {
  API_URL = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.API_URL;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;
  // private readonly API_URL = 'assets/data/professors.json';
  dataChange: BehaviorSubject<Professors[]> = new BehaviorSubject<Professors[]>(
    []
  );
  // Temporarily stores data from dialogs
  dialogData: any;
  constructor(private httpClient: HttpClient) {}
  get data(): Professors[] {
    return this.dataChange.value;
  }
  getDialogData() {
    return this.dialogData;
  }
  /** CRUD METHODS */
  getAllProfessorss(): void {
    this.httpClient.get<Professors[]>(this.API_URL).subscribe(
      data => {
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  createPhoneCallLog(
    name: string,
    call_duration: any,
    description: string,
    follow_up_date: any,
    note: string,
    call_type: string,
  ): Observable<PhoneCallLog> {
    const url = this.baseurl + '/web/create/phone_call_log/';
    const formData = new FormData();
    formData.append('name', name);
    formData.append('call_duration', call_duration);
    formData.append('description', description);
    formData.append('follow_up_date', follow_up_date);
    formData.append('note', note);
    formData.append('call_type', call_type);
    console.log("create phone log")
    return this.httpClient.request<PhoneCallLog>('POST', url, {body: formData});
  }
  // DEMO ONLY, you can find working methods below
  addProfessors(professors: Professors): void {
    this.dialogData = professors;
  }
  updateProfessors(professors: Professors): void {
    this.dialogData = professors;
  }
  deleteProfessors(id: number): void {
    console.log(id);
  }

  createSetup(
    template: string,
    about_us: string,
    logo: any,
    contact_us: string,
    header_content: string,
    footer_content: string,
    color: string,
  ): Observable<Setup> {
    const url = this.baseurl + '/web/create/setup/';
    const formData = new FormData();
    formData.append('template', template);
    formData.append('about_us', about_us);
    formData.append('logo', logo);
    formData.append('contact_us', contact_us);
    formData.append('header_content', header_content);
    formData.append('footer_content', footer_content);
    formData.append('color', color);
    return this.httpClient.request<Setup>('POST', url, {body: formData});
  }

  createAdmissionEnquiry(
    name: string,
    phone_number: string,
    email: string,
    address: string,
    description: string,
    note: string,
    no_child_to_enroll: any,
    reference: string,
    follow_up_date: any,
    status: string,
    admin_note: string
  ): Observable<AdmissionEnquiry> {
    const url = this.baseurl + '/web/create/admission_entry/';
    const formData = new FormData();
    formData.append('name', name);
    formData.append('phone_number', phone_number);
    formData.append('email', email);
    formData.append('address', address);
    formData.append('description', description);
    formData.append('note', note);
    formData.append('no_child_to_enroll', no_child_to_enroll);
    formData.append('reference', reference);
    formData.append('follow_up_date', follow_up_date);
    formData.append('status', status);
    formData.append('admin_note', admin_note);
    return this.httpClient.request<AdmissionEnquiry>('POST', url, {body: formData});
  }

  createCorrespondence(
    from_title: string,
    to_title: string,
    reference_no: any,
    address: any,
    note: string,
    date: any,
    attach_document: any
  ): Observable<Correspondence> {
    const url = this.baseurl + '/web/create/correspondence/';
    const formData = new FormData();
    formData.append('from_title', from_title);
    formData.append('to_title', to_title);
    formData.append('reference_no', reference_no);
    formData.append('address', address);
    formData.append('note', note);
    formData.append('date', date);
    formData.append('attach_document', attach_document);
    return this.httpClient.request<Correspondence>('POST', url, {body: formData});
  }

  createComplaint(
    complain_type: string,
    source: any,
    phone_number: any,
    description: string,
    action_taken: string,
    note: string,
    attach_document: any
  ): Observable<Complaint> {
    const url = this.baseurl + '/web/create/complaint/';
    const formData = new FormData();
    formData.append('complain_type', complain_type);
    formData.append('source', source);
    formData.append('phone_number', phone_number);
    formData.append('description', description);
    formData.append('action_taken', action_taken);
    formData.append('note', note);
    formData.append('attach_document', attach_document);
    return this.httpClient.request<Complaint>('POST', url, {body: formData});
  }

  createVisitorBook(
    name: string,
    phone_number: string,
    purpose: string,
    picture: any,
    note: string,
    approved: any
  ): Observable<VistorBook> {
    const url = this.baseurl + '/web/create/visitor_book/';
    const formData = new FormData();
    formData.append('phone_number', phone_number);
    formData.append('purpose', purpose);
    formData.append('picture', picture);
    formData.append('note', note);
    formData.append('approved', approved);
    return this.httpClient.request<VistorBook>('POST', url, {body: formData});
  }
}
