import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Correspondence } from './correspondence';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { finalize, tap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';
@Injectable({
  providedIn: 'root'
})
export class CorrespondenceService {

  API_URL = environment.apiUrl;
  DOMAIN_URL = environment.clientapiUrl;
  PROTOCOL = "http://"
  baseurl = this.API_URL;
  domainurl = this.DOMAIN_URL;
  protocol = this.PROTOCOL;
  // private readonly API_URL = 'assets/data/professors.json';
  dataChange: BehaviorSubject<Correspondence[]> = new BehaviorSubject<Correspondence[]>(
    []
  );
  // Temporarily stores data from dialogs
  dialogData: any;
  constructor(private httpClient: HttpClient) {}
  get data(): Correspondence[] {
    return this.dataChange.value;
  }
  getDialogData() {
    return this.dialogData;
  }
  /** CRUD METHODS */
  getAllCorrespondence(): void {
    this.httpClient.get<Correspondence[]>(this.baseurl + '/web/create/correspondence/').subscribe(
      data => {
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  
  // DEMO ONLY, you can find working methods below
  addCorrespondence(correspondence: Correspondence): void {
    this.dialogData = correspondence;
    this.createCorrespondence(correspondence);
  }
  updateCorrespondence(correspondence: Correspondence): void {
    this.editCorrespondence(correspondence);
    this.dialogData = correspondence;
  }
  deleteCorrespondence(id: any): void {
    console.log(id);
    this.deleteACorrespondence(id);
  }

  createCorrespondence(
    // from_title: string,
    // to_title: string,
    // reference_no: any,
    // address: any,
    // note: string,
    // date: any,
    // attach_document: any
    correspondence: Correspondence
  ): Observable<Correspondence> {
    const url = this.baseurl + '/web/create/correspondence/';
    const formData = new FormData();
    formData.append('from_title', correspondence.from_title);
    formData.append('to_title', correspondence.to_title);
    formData.append('reference_no', correspondence.reference_no);
    formData.append('address', correspondence.address);
    formData.append('call_type', correspondence.call_type);
    formData.append('note', correspondence.note);
    formData.append('date', correspondence.date);
    // formData.append('attach_document', correspondence.attach_document);
    return this.httpClient.request<Correspondence>('POST', url, {body: formData});
  }

  editCorrespondence(
    correspondence: Correspondence
  ): Observable<Correspondence> {
    const url = this.baseurl + '/web/edit/correspondence/' + correspondence.id + '/';
    const formData = new FormData();
    formData.append('from_title', correspondence.from_title);
    formData.append('to_title', correspondence.to_title);
    formData.append('reference_no', correspondence.reference_no);
    formData.append('address', correspondence.address);
    formData.append('call_type', correspondence.call_type);
    formData.append('note', correspondence.note);
    formData.append('date', correspondence.date);
    // formData.append('attach_document', correspondence.attach_document);
    return this.httpClient.request<Correspondence>('PUT', url, {body: formData});
  }

  deleteACorrespondence(
    id: any
  ): void {
    console.log("deleted");
    this.httpClient.delete<Correspondence>(this.baseurl + '/web/edit/correspondence/' + id + '/').subscribe(
      data => {
        console.log(data);
      },
      (error: HttpErrorResponse) => {
        console.log(error.name + ' ' + error.message);
      }
    );
  }

  
}
